import { useState, useRef, useEffect } from "react"
import {
  BRIDE_FULLNAME,
  GROOM_FULLNAME,
  LOCATION,
  WEDDING_DATE,
} from "../../const"
import coverImage from "../../image/cover.png"
import myMusic from "../../image/bgm.mp3"
import { ReactComponent as AudioOnIcon } from "../../image/audio-on.svg"
import { ReactComponent as AudioOffIcon } from "../../image/audio-off.svg"
import { LazyDiv } from "../lazyDiv"
import { Button } from "../button"
import Typewriter from 'typewriter-effect';

export const Cover = () => {
  const [isPlaying, setIsPlaying] = useState(true); // ✅ 자동 재생을 위해 기본값 true
   const audioRef = useRef<HTMLAudioElement | null>(null); // ✅ 타입 명시

  useEffect(() => {
    // 오디오 인스턴스를 만들어서 ref에 저장
    const audio = new Audio(myMusic);
    audio.loop = true;
    audioRef.current = audio;

    // 자동 재생 시도
    const tryPlay = async () => {
      try {
        await audio.play();
        setIsPlaying(true);
      } catch (err) {
        setIsPlaying(false);
      }
    };

    tryPlay();

    return () => {
      audio.pause();
      audio.currentTime = 0;
    };
  }, []);

  const togglePlay = () => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isPlaying) {
      audio.pause();
      audio.currentTime = 0;
    } else {
      audio.play().catch((err) => {
      });
    }
    setIsPlaying(!isPlaying);
  };

  return (
    <LazyDiv className="card cover">
      <div className='music'>
        <Button className='music-button' onClick={togglePlay}>
          {isPlaying ? <AudioOnIcon /> : <AudioOffIcon />}
        </Button>
      </div>

       <h1 className="english">
        <Typewriter
          options={{ wrapperClassName: "english", delay: 80,}}
          onInit={(typewriter) => {
            typewriter
              .callFunction((state) => {
                state.elements.cursor.style.display = 'none'
              })
                .typeString('<span class="wedding-text">We´re Getting Married </span>')
                //.typeString('<br /><span class="invitation-text">CELEBRATE WITH US</span>')
              // .typeString('<span class="wedding-text">Welcome to</span>')
              // .typeString('<br /><span class="invitation-text">the Celebration of Love</span>')
              //.typeString('WEDDING')
              //.typeString('<br />INVITATION')
              .start()
          }}
        />
        
      </h1> 
      {/* <h1 className="english1">WEDDING</h1>*/}
      <div className="invitation-text">CELEBRATE WITH US</div>
      
      
      <div className="image-row">
      <div className="groom-name">{GROOM_FULLNAME}</div>

      <div className="image-wrapper">
        <img src={coverImage} alt="sample" />
      </div>

      {/* <div className="subtitle">The wedding of</div> */}
      <div className="bride-name">{BRIDE_FULLNAME}</div>
      </div>
       
      <div className="names">
        {/* {GROOM_FULLNAME}&nbsp;<HeartIcon />&nbsp;{BRIDE_FULLNAME} */}
      </div>

      <div className="info">
        {WEDDING_DATE.format("YYYY년 MMMM D일 dddd A h시")}
        <br /> {LOCATION} 41층 파노라마홀
        <br />
        <br />
      </div>
    </LazyDiv>
  )
}